import Vue from "vue";
import App from "./App.vue";
import router from "./router/router";
import store from "./store/store";
import VueLazyload from "vue-lazyload";
import "./cube-ui";
//vue项目中icon图标的完美引入 http://www.cnblogs.com/zhaozhou/p/10248949.html
import "./assets/icon/iconfont.css";
import toTop from "./components/toTop";
import buyHeader from "./components/buyHeader";

Vue.config.productionTip = false;
Vue.use(VueLazyload);

// 全局组件注册
const nav = {
  install: function(Vue) {
    Vue.component("toTop", toTop);
    Vue.component("buyHeader", buyHeader);
  }
};
// 跳转后返回顶部
router.afterEach(() => {
  window.scrollTo(0, 0);
});
Vue.use(nav);
new Vue({
  router,
  store,
  render: h => h(App)
}).$mount("#app");
