import axios from "axios";
//封装请求
//env.VUE_APP_API_URL .evn.development/.evn.production 中的路径
const http = {
  get: function(url) {
    //get请求
    return new Promise((resolve, reject) => {
      axios
        .get(process.env.VUE_APP_API_URL + url)
        .then(response => {
          resolve(response.data);
        })
        .catch(error => {
          reject(error);
        });
    });
  },
  post: function(url, params) {
    //post请求
    return new Promise((resolve, reject) => {
      axios
        .post(process.env.VUE_APP_API_URL + url, params)
        .then(response => {
          resolve(response.data);
        })
        .catch(error => {
          reject(error);
        });
    });
  },
  load: function(url) {
    //用来请求本地json文件
    return new Promise((resolve, reject) => {
      // Object.assign(params)
      axios
        .get(url)
        .then(response => {
          resolve(response.data);
        })
        .catch(error => {
          reject(error);
        });
    });
  },
  orderpost: function(url, params) {
    //下单查单请求
    return new Promise((resolve, reject) => {
      axios
        .post(process.env.VUE_APP_ORDER_URL + url, params)
        .then(response => {
          resolve(response.data);
        })
        .catch(error => {
          reject(error);
        });
    });
  },
  conmmget: function(url) {
    return new Promise((resolve, reject) => {
      axios
        .get(process.env.VUE_APP_COMM_URL + url)
        .then(response => {
          resolve(response.data);
        })
        .catch(error => {
          reject(error);
        });
    });
  },
  conmmpost: function(url, params) {
    //post请求
    return new Promise((resolve, reject) => {
      axios
        .post(process.env.VUE_APP_COMM_URL + url, params)
        .then(response => {
          resolve(response.data);
        })
        .catch(error => {
          reject(error);
        });
    });
  }
};
export default http;
