<template>
  <div id="app" :style="appHeight">
    <!--    <keep-alive></keep-alive>  在router-view上面搭配:key="$route.fullPath"使用  keep-alive是Vue的内置组件，能在组件切换过程中将状态保留在内存中，防止重复渲染DOM（用于解决相同路由下只变换参数但页面数据不会刷新的问题），虽然这种方法一劳永逸但是会出现短暂空白以及对性能造成一定损耗，不适用于一个tab切换路由并加载列表的组件，会造成页面白屏，dev模式不会自动刷新，是个坑 -->
    <router-view></router-view>
    <toTop></toTop>
  </div>
</template>

<script>
import { site } from "./lib/goods";
import { mapMutations, mapState } from "vuex";

export default {
  data() {
    return {
      appHeight: {
        height: window.innerHeight + "px"
      }
    };
  },
  async created() {
    this.getSiteInfo();
  },
  mounted() {
    let favicon = this.siteInfo.favicon;
    var head = document.getElementsByTagName("head")[0],
      linkTag = document.createElement("link");
    linkTag.setAttribute("rel", "icon");
    linkTag.setAttribute("href", this.cdnbase + favicon);
    head.appendChild(linkTag);
  },
  methods: {
    ...mapMutations(["setSite", "setSiteClass", "setLanguage", "setConfig"]),
    async getSiteInfo() {
      let data = await site.get_site();
      if (!data.error) {
        this.setSite(data.site);
        this.setSiteClass(data.site_class);
        this.setLanguage(data.lang);
        this.setConfig(data.config);
      }
    }
  },
  computed: {
    ...mapState(["language", "siteInfo"]),
    lang() {
      return function(str) {
        return this.language ? this.language[str] : "";
      };
    }
  }
};
</script>

<style>
@import "./assets/icon/iconfont.css";
</style>

<style lang="stylus">
@import './assets/css/base.css';
#app
  font-family 'Avenir', Helvetica, Arial, sans-serif
  -webkit-font-smoothing antialiased
  -moz-osx-font-smoothing grayscale
  text-align center
  color #2c3e50
  max-width: 768px;
  margin: 0 auto

  .toTop
    position fixed
    right 15px
    bottom 60px
    width 38px
    height 38px
    border 1px solid #dedede
    border-radius 50%
    padding-top 5px
    box-sizing border-box
    background #FFFFFF

    .buy-icon-fanhui1
      font-size 24px
      color #c5c5c5

#nav
  padding 30px

  a
    font-weight bold
    color #2c3e50

    &.router-link-exact-active
      color #42b983
</style>
