import Vue from "vue";
import Router from "vue-router";

Vue.use(Router);

export default new Router({
  mode: "history",
  base: process.env.BASE_URL,
  routes: [
    {
      path: "/",
      name: "home",
      component: () => import("../views/Home.vue")
    },
    {
      path: "/:id",
      component: () => import("../views/Home.vue")
    },
    {
      path: "/classify",
      name: "classify",
      component: () =>
        import(/* webpackChunkName: "classifyPage" */ "../views/classifyPage.vue")
    },
    {
      path: "/category/:id",
      name: "category",
      component: () =>
        import(/* webpackChunkName: "category" */ "../views/category.vue")
    },
    {
      path: "/search",
      name: "search",
      component: () =>
        import(/* webpackChunkName: "searchPage" */ "../views/searchPage.vue")
    },
    {
      path: "/detail/:typeid/:id",
      name: "detail",
      component: () =>
        import(/* webpackChunkName: "Detail" */ "../views/Detail.vue")
    }
  ]
});
