import Vue from "vue";
import Vuex from "vuex";
import createPersistedState from "vuex-persistedstate";
Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    //网站基本信息
    siteInfo: {},
    //语言
    language: {},
    //分类信息
    classInfo: [],
    //信用卡支付信息，图片网址
    config: {},
    skuFlag: false,
    prod: {},
    cartList: []
  },
  mutations: {
    setSite(state, data) {
      state.siteInfo = data;
    },
    setLanguage(state, data) {
      state.language = data;
    },
    setSiteClass(state, data) {
      state.classInfo = data;
    },
    setConfig(state, data) {
      state.config = data;
    },
    setCartList(state, data) {
      state.cartList = data;
    }
  },
  actions: {},
  getters: {
    getSite: state => {
      return state.siteInfo;
    }
  },
  plugins: [
    createPersistedState({
      storage: window.localStorage,
      reducer(val) {
        return {
          orderInfo: val.orderInfo,
          classInfo: val.classInfo,
          language: val.language,
          siteInfo: val.siteInfo,
          config: val.config,
          cartList: val.cartList
        };
      }
    })
  ]
});
