//页面所有请求
//引入http.js 使用其中封装的请求
import http from "./http";
//获取页面主要信息
export const site = {
  get_site: () => {
    // 首页商品楼层信息
    let url = `basic`;
    return http.get(url);
  }
};

//home页面的请求
export const home = {
  // 获取首页信息
  get_home_goods: () => {
    // 首页商品楼层信息
    let url = `home`;
    return http.get(url);
  }
};

export const classify = {
  // 分类列表
  get_home_productAll: ({ id, mid }) => {
    console.log({ id, mid });
    let url;
    if (mid === undefined) {
      url = `class/${id}`;
    } else {
      url = `class/${id}/${mid}`;
    }
    return http.get(url);
  }
};

export const search = {
  //搜索商品
  get_shearch_goods: str => {
    let url = `search?searchkey=` + str;
    return http.post(url);
  }
};

export const detail = {
  //获取当前商品的详情数据
  get_detail_info: that => {
    let params = that.$route.params;
    console.log(that.$route);
    let url = `detail/${params.typeid}/${params.id}`;
    return http.get(url);
  },
  //获取当前商品的评论
  get_detail_comm: id => {
    let url = `comment/list/${id}`;
    return http.get(url);
  }
};
